// app/dashboard/page.jsx
// Dashboard with animated logo navbar, correct greeting,
// rotating daily subheadings, dynamic quick-nav, reordering button,
// and improved Foods card (scrollable + gradient).

import Link from "next/link";
import Logo from "../components/logo";
import { headers } from "next/headers";

// ---------------------------------------------------------
// FETCH SUMMARY
// ---------------------------------------------------------
async function getSummary() {
  const headerStore = await headers();
  const host =
    headerStore.get("x-forwarded-host") ||
    headerStore.get("host") ||
    "localhost:3000";
  const protocol =
    headerStore.get("x-forwarded-proto") ||
    (host.includes("localhost") ? "http" : "https");
  const baseUrl = `${protocol}://${host}`;

  const res = await fetch(`${baseUrl}/api/summary`, { cache: "no-store" });
  if (!res.ok) {
    const err = await res.json().catch(() => ({}));
    throw new Error(err.error || `Summary fetch failed: ${res.status}`);
  }
  return res.json();
}

// ---------------------------------------------------------
// DASHBOARD
// ---------------------------------------------------------
export default async function DashboardPage() {
  let summary;
  try {
    summary = await getSummary();
  } catch (e) {
    return (
      <main className="min-h-screen bg-slate-50 text-slate-900 flex items-center justify-center">
        <div className="bg-white shadow rounded-xl p-6 border border-slate-200 max-w-md text-center">
          <p className="font-semibold text-slate-900 mb-2">Unable to load dashboard</p>
          <p className="text-slate-600 text-sm">{e.message}</p>
          <Link href="/login" className="mt-4 inline-block text-blue-700 hover:underline">
            Return to login
          </Link>
        </div>
      </main>
    );
  }

  const {
    user,
    caloriesTarget = 0,
    caloriesConsumed = 0,
    macros = { protein: 0, carbs: 0, fat: 0 },
    foods = [],
    workouts = [],
  } = summary;

  // CURRENT ROUTE (for filtering quick-nav)
  const headersList = await headers();
  const currentPath =
    headersList.get("x-invoke-path") ||
    headersList.get("x-pathname") ||
    "";

  const navItems = [
    { name: "Dashboard", href: "/dashboard" },
    { name: "Log Food", href: "/food/log" },
    { name: "Manage Foods", href: "/food/manage" },
    { name: "Log Workout", href: "/workout/log" },
    { name: "Summary", href: "/summary" },
    { name: "Settings", href: "/settings" },
  ];

  const filteredNav = navItems.filter((item) => item.href !== currentPath);

  // NAME LOGIC (no placeholder; empty if not available)
  const name = user?.preferredName?.trim() || user?.firstName?.trim() || "";

  // DATE
  const today = new Date().toLocaleDateString("en-US", {
    weekday: "long",
    month: "short",
    day: "numeric",
  });

  // ROTATING SUBHEADING
  const messages = [
    "Here’s your day at a glance.",
    "Let’s make today a strong one.",
    "Small steps, big movement.",
    "Fuel smart. Feel better.",
    "Your habits are building momentum.",
    "Every log gets you closer.",
    "Consistency outperforms intensity.",
    "Today is a great day to improve.",
    "Keep showing up. It works.",
    "Precision fuels progress.",
    "Good nutrition starts with awareness.",
    "Little adjustments, big long-term wins.",
    "Fuel choices shape performance.",
    "Eat for the work you want to do.",
    "Recovery starts with fueling right.",
    "Energy in → energy out.",
    "Training smarter starts here.",
    "The best athletes track.",
    "Let’s build something solid today.",
    "Proud of you for checking in.",
    "Your future self appreciates this.",
    "Keep going. You're doing better than you think.",
    "Slow and steady is still forward.",
    "Dashboard initialized.",
    "System calibrated for today.",
    "Fuel metrics updated.",
    "Your nutrition arc continues.",
    "Hydrate. Then dominate.",
  ];

  const idx = new Date().getDate() % messages.length;
  const dailyMessage = messages[idx];

  const remaining = Math.max(caloriesTarget - caloriesConsumed, 0);
  const ringCirc = 2 * Math.PI * 42;
  const progressRatio =
    caloriesTarget > 0 ? Math.min(caloriesConsumed / caloriesTarget, 2) : 0;
  const macroTotal = Math.max(macros.protein + macros.carbs + macros.fat, 1);
  const proteinPct = (macros.protein / macroTotal) * 100;
  const carbsPct = (macros.carbs / macroTotal) * 100;
  const fatPct = (macros.fat / macroTotal) * 100;

  const overage = caloriesConsumed - caloriesTarget;
  const statusMessage =
    overage >= 400
      ? "You are significantly above your target today."
      : remaining <= 500 && remaining > 0
      ? "You're close to your target. Plan your next meal carefully."
      : "";

  return (
    <main className="min-h-screen bg-slate-50 text-slate-900">

      {/* NAVBAR */}
      <nav className="w-full flex items-center justify-between px-8 py-4 bg-white shadow-sm">
        <Link href="/dashboard" className="flex items-center gap-2 cursor-pointer">
          <Logo className="h-8 w-8" />
        </Link>
        <div className="text-sm text-slate-500">{today}</div>
      </nav>

      {/* GREETING */}
      <section className="mt-10 flex flex-col items-center">
        <h1 className="text-3xl font-semibold text-slate-900">
          {name ? `Hello, ${name}.` : "Welcome to ZeroFlow."}
        </h1>
        <p className="text-slate-500 mt-1">{dailyMessage}</p>
        {statusMessage && (
          <p className="mt-2 text-sm font-medium text-red-600">{statusMessage}</p>
        )}
      </section>

      {/* GRID CARDS */}
      <section className="grid grid-cols-1 md:grid-cols-2 gap-6 px-8 mt-12">

{/* CALORIES CARD */}
<div className="bg-white shadow-lg rounded-xl p-6 border border-slate-100 flex flex-col gap-6">

  {/* ---------- TOP: Progress Ring + numbers ---------- */}
  <div className="flex items-center gap-6">

    {/* Progress Ring */}
    <div className="relative h-24 w-24">
      <svg className="h-full w-full transform -rotate-90">
        <circle
          cx="48" cy="48" r="42"
          stroke="#E2E8F0"            // slate-200
          strokeWidth="10"
          fill="none"
        />
        <circle
          cx="48" cy="48" r="42"
          stroke="#1E40AF"            // blue-800
          strokeWidth="10"
          fill="none"
          strokeDasharray={ringCirc}
          strokeDashoffset={(1 - progressRatio) * ringCirc}
          strokeLinecap="round"
          className="transition-all duration-700 ease-out"
        />
      </svg>

      {/* Center text */}
      <div className="absolute inset-0 flex items-center justify-center">
        <span className="text-lg font-semibold text-slate-800">
          {caloriesTarget > 0 ? Math.round((caloriesConsumed / caloriesTarget) * 100) : 0}%
        </span>
      </div>
    </div>

    {/* Numbers */}
    <div>
      <h2 className="text-xl font-semibold mb-3">Daily Calories</h2>
      <p><span className="font-medium">Target:</span> {caloriesTarget} cal</p>
      <p><span className="font-medium">Consumed:</span> {caloriesConsumed} cal</p>
      <p
        className={`font-medium mt-2 ${
          remaining === 0 ? "text-red-600" : "text-green-700"
        }`}
      >
        Remaining: {remaining} cal
      </p>
    </div>
  </div>

  {/* ---------- MACRO BAR ---------- */}
  <div className="mt-1">
    <p className="font-medium text-slate-700 mb-2">Macros</p>

    <div className="flex items-center gap-3 text-sm text-slate-600 mb-2">
      <span>Protein: {macros.protein}g</span>
      <span>Carbs: {macros.carbs}g</span>
      <span>Fat: {macros.fat}g</span>
    </div>

    <div className="w-full h-3 bg-slate-200 rounded-full overflow-hidden flex">
      <div className="bg-blue-700 h-full" style={{ width: `${proteinPct}%` }} />
      <div className="bg-blue-400 h-full" style={{ width: `${carbsPct}%` }} />
      <div className="bg-blue-900 h-full" style={{ width: `${fatPct}%` }} />
    </div>
  </div>

</div>


        {/* FOODS CARD — UPDATED */}
        <div className="bg-white shadow-lg rounded-xl p-6 border border-slate-100">
          <h2 className="text-xl font-semibold mb-4">Foods Today</h2>

          {foods.length === 0 ? (
            <p className="text-slate-600 text-sm">No foods logged yet.</p>
          ) : (
<div className="max-h-32 overflow-y-auto pr-2 relative">
  <ul className="flex flex-col gap-3">
    {foods.map((food, idx) => (
      <li
        key={idx}
        className="flex justify-between border-b pb-2 text-slate-700"
      >
        <div className="flex flex-col">
          <span>{food.name}</span>
          <span className="text-xs text-slate-500">
            {food.protein}g P · {food.carbs}g C · {food.fat}g F
          </span>
        </div>
        <div className="text-right">
          <span>{food.calories} cal</span>
          {food.quantity ? (
            <span className="block text-xs text-slate-500">x{food.quantity}</span>
          ) : null}
        </div>
      </li>
    ))}
  </ul>

  {/* Gradient fade at bottom */}
  <div className="pointer-events-none absolute bottom-0 left-0 w-full h-10 bg-gradient-to-t from-white to-transparent" />
</div>

          )}

          <Link href="/food/log" className="inline-block mt-6 text-blue-700 hover:underline">
            Log Food →
          </Link>
        </div>

        {/* WORKOUTS CARD */}
        <div className="bg-white shadow-lg rounded-xl p-6 border border-slate-100">
          <h2 className="text-xl font-semibold mb-4">Workouts</h2>

          {workouts.length === 0 ? (
            <p className="text-slate-600 text-sm">No workouts logged yet.</p>
          ) : (
            <ul className="flex flex-col gap-3">
              {workouts.map((w, idx) => (
                <li
                  key={idx}
                  className="flex justify-between border-b pb-2 text-slate-700"
                >
                  <span>{w.type} · {w.duration} min</span>
                  <span className="text-green-700">{w.caloriesBurned} cal</span>
                </li>
              ))}
            </ul>
          )}

          <Link href="/workout/log" className="inline-block mt-6 text-blue-700 hover:underline">
            Log Workout →
          </Link>
        </div>

      </section>

      {/* REORDER BUTTON */}
      <button
        className="
          fixed bottom-6 right-6 rounded-full w-12 h-12 bg-blue-700 
          text-white shadow-lg hover:bg-blue-800 flex items-center 
          justify-center text-xl
        "
        title="Reorder Dashboard"
      >
        ⠿
      </button>

      {/* QUICK NAV */}
      <div className="fixed bottom-20 right-6">
        <details className="group relative">
          <summary
            className="
              w-12 h-12 bg-slate-900 text-white rounded-full cursor-pointer
              shadow-lg flex items-center justify-center text-xl
              hover:bg-slate-800 list-none
            "
            title="Quick Navigation"
          >
            ☰
          </summary>

          <div className="absolute bottom-14 right-0 bg-white shadow-xl rounded-xl p-4 border border-slate-100 w-40">
            <div className="flex flex-col gap-2 text-sm">

              {filteredNav.map((item) => (
                <Link key={item.href} href={item.href} className="hover:text-blue-700">
                  {item.name}
                </Link>
              ))}

              <div className="border-t border-slate-200 pt-2 mt-2">
                <Link href="/login" className="text-red-600 hover:text-red-700">
                  Log Out
                </Link>
              </div>

            </div>
          </div>
        </details>
      </div>
    </main>
  );
}
